import React, { FC } from 'react';
import { Redirect, Route, Switch, useRouteMatch } from 'react-router-dom';

const About = React.lazy(() => import('../pages/About/About'));
const Game = React.lazy(() => import('../pages/GamePlay/Game'));
const MainMenuRouter = React.lazy(() => import('./MainMenuRouter'));

export const MENU_PATH = 'menu';

const AppRouter: FC = () => {
    const { path } = useRouteMatch();

    return (
        <Switch>
            <Route path={`${path}about`} exact component={About} />

            <Route path={`${path}single`} exact>
                <Game type="single" />
            </Route>
            <Route path={`${path}multiplayer`} exact>
                <Game type="multiplayer" />
            </Route>

            <Route path={`${path}${MENU_PATH}/`}>
                <MainMenuRouter />
            </Route>

            <Route>
                <Redirect to={`${path}${MENU_PATH}/`} />
            </Route>
        </Switch>
    );
};

export default AppRouter;
